import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MaterialModule } from 'src/app/shared/material.module';
import { UserhelpRoutingModule } from './userhelp-routing.module';
import { UserhelpComponent } from './userhelp.component';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { HttpLoaderFactory } from 'src/app/managetisuser/newtisuser/newtisuser.module';
import { HttpClient } from '@angular/common/http';

// User help module.
@NgModule({
  declarations: [UserhelpComponent],
  imports: [
    CommonModule,
    MaterialModule,
    TranslateModule.forRoot(
      {
        loader: {
          provide: TranslateLoader,
          useFactory: HttpLoaderFactory,
          deps: [HttpClient]
        }
      }),
    UserhelpRoutingModule
  ]
})
export class UserhelpModule { }
