import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { Constants } from '../utils/constants';
import { environment } from 'src/environments/environment';
import { TranslateService } from '@ngx-translate/core';
import { MediaObserver, MediaChange } from '@angular/flex-layout';
import { Auth } from 'aws-amplify';

@Component({
  selector: 'app-userhelp',
  templateUrl: './userhelp.component.html',
  styleUrls: ['./userhelp.component.css']
})

export class UserhelpComponent implements OnInit {

  /** To specify default language in which the form will appear. */
  private _defaultLang: string;
  flexMediaWatcher: any;

  /**
   * Accessor for the defaultLang property
   * @return Current value of the language property.
   */
  public get defaultLang(): string {
    return this._defaultLang;
  }

  /** Supported application languages. */
  lang: string[] = Constants.SUPPORTED_LANGUAGES;

  /**
   * Mutator for the defaultLang property
   * @param language New value of the language property.
   */
  public set defaultLang(value: string) {
    this._defaultLang = value;
  }

  /** URL for getting user help page. */
  helpUrl: string = environment.user_help_url;

  /** Save Sanetized URL rendering into iFram. */
  urlSafe: SafeResourceUrl;

  /**Member variable to identify source page of help clicked. */
  sourcePageName: string = Constants.USER_LIST_FORM;

  /** Show/hide navigation lists based on screen width.  */
  showNavListButtons: boolean = true;

  /** Holds current screen width, updated as a result of media change. */
  currentScreenWidth: string = '';

  /**
   * Constructor with injected sanitizer and translate.
   * @param sanitizer - To make url for rendering into iFram
   * @param translate - To support multi language
   */
  constructor(public sanitizer: DomSanitizer,
    private media: MediaObserver,
    private translate: TranslateService) {
    this.defaultLang = Constants.DEFAULT_LANGUAGE;
    translate.addLangs(this.lang);
    translate.setDefaultLang(this.defaultLang);

    this.flexMediaWatcher = media.media$.subscribe((change: MediaChange) => {
      if (change.mqAlias !== this.currentScreenWidth) {
        this.currentScreenWidth = change.mqAlias;
        if (this.currentScreenWidth == 'xs' || this.currentScreenWidth == 'sm') {
          this.showNavListButtons = false;
        } else {
          this.showNavListButtons = true;
        }
      }
    });
  }

  /**
   * Implement OnInit's `ngOnInit` method.
   */
  ngOnInit() {
    if (null != Auth.currentAuthenticatedUser()) {
      // Default url for help page.
      this.helpUrl = this.helpUrl + this.defaultLang + Constants.FORWARD_SLASH + Constants.USER_HELP_MAIN;
      let helpSourcePage = localStorage.getItem('sourcePageOfHelp');
      if (null != helpSourcePage) {
        this.helpUrl = environment.user_help_url + this.defaultLang + Constants.FORWARD_SLASH;
        this.sourcePageName = helpSourcePage;
        if (this.sourcePageName == Constants.ADD_USER_FORM) {
          this.helpUrl = this.helpUrl + Constants.USER_HELP_ADD;
        } else if (this.sourcePageName == Constants.EDIT_USER_FORM) {
          this.helpUrl = this.helpUrl + Constants.USER_HELP_EDIT;
        } else if (this.sourcePageName == Constants.USER_LIST_FORM) {
          this.helpUrl = this.helpUrl + Constants.USER_HELP_LANDING;
        }
      }
      this.urlSafe = this.sanitizer.bypassSecurityTrustResourceUrl(this.helpUrl);
    }
  }

  /**
   * Navigation list click event
   * @param selectedNavList - Paramenter to identify which help page needs to open 
   */
  navClicked(selectedNavList: string) {
    Auth.currentAuthenticatedUser().then(x => {
      this.helpUrl = environment.user_help_url + this.defaultLang + Constants.FORWARD_SLASH;
      if (selectedNavList == "feature") {
        this.helpUrl = this.helpUrl + Constants.USER_HELP_MAIN;
      } else if (selectedNavList == "userList") {
        this.helpUrl = this.helpUrl + Constants.USER_HELP_LANDING;
      } else if (selectedNavList == "addUser") {
        this.helpUrl = this.helpUrl + Constants.USER_HELP_ADD;
      } else if (selectedNavList == "editUser") {
        this.helpUrl = this.helpUrl + Constants.USER_HELP_EDIT;
      } else if (selectedNavList == "userAccount") {
        this.helpUrl = this.helpUrl + Constants.USER_HELP_LOGIN_OUT;
      } else if (selectedNavList == "forcePasswordChange") {
        this.helpUrl = this.helpUrl + Constants.USER_HELP_FORCE_PASSWORD;
      } else if (selectedNavList == "makeUserInactive") {
        this.helpUrl = this.helpUrl + Constants.USER_HELP_MAKE_INACTIVE;
      } else if (selectedNavList == "error") {
        this.helpUrl = this.helpUrl + Constants.USER_HELP_ERROR;
      }
      this.urlSafe = this.sanitizer.bypassSecurityTrustResourceUrl(this.helpUrl);
    }).catch(y => {
      const redirectUrlParams = window.location.href;
      window.location.href = environment.auth_ui_url + '?redirect=' + redirectUrlParams;
    });
  }
}
